.expand {
    font-family: TTLakes-Regular, sans-serif;
    color: #1b1c21;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }
  .expand::before {
    content: url(./static/show.svg);
    margin-right: 16px;
    margin-left: 8px;
    transform: translate(-1px, 1px);
  }
  
  .expand--close::before {
    content: url(./static/hide.svg);
  }
  